.tags-container {
  display: flex;
  overflow-x: auto;
  width: 100%;
  max-width: 100%;
  border: 1px grey solid;
  border-radius: 5px;
  color: gray;
  margin-bottom: 10px;
}

.tags-input {
  outline: none;
  width: 100%;
  min-width: 50%;
  border: none;
  border-radius: 5px;
  padding: 14px;
}

.tag {
  display: flex;
  align-items: center;
  margin: 7px 0;

  margin-left: 10px;
  padding: 0 10px;
  padding-right: 5px;
  border-radius: 5px;
  background-color: orange;
  white-space: nowrap;
  color: white;
}

.tag button {
  display: flex;
  padding: 6px;
  border: none;
  background-color: unset;
  cursor: pointer;
  color: white;
}
